.nav-link:hover {
  transition: all 0.9s;
}

.nav-link-collapse:after {
  float: right;
  content: "\f067";
  font-family: "FontAwesome";
}

.nav-link-show:after {
  float: right;
  content: "\f068";
  font-family: "FontAwesome";
}

.nav-item ul.nav-second-level {
  padding-left: 0;
}
.nav-link {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.nav-link span {
  width: 100%;
}
.nav-item ul.nav-second-level > .nav-item {
  padding-left: 20px;
}
.vendor-sidebar {
  transition: all 0.3s ease-in-out;
}
@media (min-width: 992px) {
  .vendor-sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 200px;
    box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.07);
  }
  .usaneTitle {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    margin-top: 10vh;
    margin-bottom: 10vh;
  }
  .navbar {
    box-shadow: 3px 0px 7px rgba(0, 0, 0, 0.07);
  }
  .navbar-expand-lg .vendor-sidebar {
    flex-direction: column;
  }

  .custom-content-wrapper {
    margin-left: 200px;
    margin-top: 20px;
  }
}

@media (max-width: 991px) {
  .navbar {
    box-shadow: 3px 0px 7px rgba(0, 0, 0, 0.07);
  }
}
.dropdown-toggle::after {
  display: none;
}
.nav-link-custom .hr {
  padding: 0;
  margin-top: 3px;
  margin-left: 3px;
  border-bottom: solid 2px #ba0fb4;
  width: 0%;
  align-self: flex-start;
  transition: all 0.3s ease-in-out;
}
.nav-link-custom:hover .hr {
  width: 40%;
}
