@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.pac-container.pac-logo .pac-item,
.pac-container.pac-logo {
  z-index: 1000000;
}

body {
  overflow-x: hidden;
  font-family: "Roboto", Helvetica, sans-serif;
}
.forgot-link {
  color: rgb(115, 164, 228);
  margin-top: 10px;
}
.forgot-link:hover {
  cursor: pointer;
  color: blue;
}
.geosuggest,
.geosuggest__input-wrapper,
.geosuggest__input {
  width: 97% !important;
  margin: auto;
  height: 40px;
  z-index: 1000000000000 !important;
}
.geosuggest__suggests-wrapper,
.geosuggest__suggests,
.geosuggest__suggests--hidden,
.geosuggest__item,
.geosuggest__item__matched-text {
  z-index: 1000000000000 !important;
  background-color: white;
}
.geosuggest__suggests {
  width: 97% !important;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
  list-style-type: none;
}

.geosuggest__item {
  font-size: 15px;
  margin: 4px;
}

.login-buttons a.btn {
  background-color: #880b8f;
  border-radius: 0;
  border: solid 1px #880b8f;
  -webkit-transition-property: background-color border color;
  transition-property: background-color border color;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}
.home-navbar {
  z-index: 100;
  border-bottom: solid 1px rgba(0, 0, 0, 0.05);
}
.login-buttons a.btn:hover {
  background-color: #fff;
  border: solid 1px #880b8f;
  color: #880b8f !important;
}

.logo {
  width: 80%;
  align-self: center;
}
.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.app-btn {
  width: 45%;
  max-width: 160px;
  color: #fff;
  margin: 20px 10px;
  text-align: left;
  border-radius: 5px;
  text-decoration: none;
  font-family: "Lucida Grande", sans-serif;
  font-size: 10px;
  text-transform: uppercase;
  box-sizing: border-box;
}
.app-btn.blu {
  background-color: #101010;
  -webkit-transition: background-color 0.25s linear;
  transition: background-color 0.25s linear;
}
.app-btn.blu:hover {
  background-color: #454545;
}
.app-btn i {
  width: 20%;
  text-align: center;
  font-size: 28px;
  margin-right: 7px;
}
.app-btn .big-txt {
  font-size: 17px;
  text-transform: capitalize;
}
.app-btn:hover {
  text-decoration: none;
  color: #fff;
}
.text {
  margin-top: 5px;
}
@-webkit-keyframes logoAnimation {
  from {
    top: -70vh;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}
@keyframes logoAnimation {
  from {
    top: -70vh;
    opacity: 0;
  }
  to {
    top: 0;
    opacity: 1;
  }
}

footer {
  margin-top: 20vh;
  padding: 25px 20px;
  border-top: solid 1px rgba(0, 0, 0, 0.05);
}
footer p {
  color: rgba(0, 0, 0, 0.3);
  font-size: 16px;
  /* font-weight: bold; */
}

footer a:hover {
  text-decoration: none;
}
footer a {
  margin: 3px auto;
  font-size: 15px;
  /* font-weight: bold; */
}
footer div {
  margin-bottom: 30px;
}
footer.footer {
  background-color: rgb(245, 245, 245);
}
@media only screen and (min-width: 700px) {
  .terms {
    position: relative;
    left: 35px;
  }
}

ul {
  list-style-type: lower-roman;
}
h1 {
  /* color: rgb(206, 32, 206); */
  font-weight: 700;
  margin-bottom: 5vh;
  text-transform: capitalize;
}

h3 {
  /* color: rgb(206, 32, 206); */
  font-weight: 700;
  margin-top: 5vh;
  text-transform: capitalize;
}


.contact-form {
  margin-top: 10vh;
  margin-bottom: 10vh;
  border: solid 1px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 4px;
}
.contact-form button {
  background-color: #880b8f;
  border: solid 1px #880b8f;
  margin-top: 20px;
  -webkit-transition-property: background-color color;
  transition-property: background-color color;
  color: #fff;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}
.contact-form button:hover {
  background-color: #fff;
  color: #880b8f;
}
.contact-form legend {
  font-size: 30px;
  margin-bottom: 20px;
}

.loginAdmin {
  margin-top: 10vh;
  border-radius: 5px;
  border: solid 1px rgba(0, 0, 0, 0.1);
  padding: 10px;
  margin-bottom: 12vh;
}
.loginAdmin legend {
  font-size: 30px;
  margin-bottom: 20px;
}

.loginAdmin button {
  background-color: #880b8f;
  border: solid 1px #880b8f;
  margin-top: 20px;
  -webkit-transition-property: background-color color;
  transition-property: background-color color;
  color: #fff;
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-in-out;
          transition-timing-function: ease-in-out;
}
.loginAdmin button:hover {
  background-color: #fff;
  color: #880b8f;
}

table {
  margin-top: 3vh;
}
table ion-icon[name="trash"] {
  color: rgb(145, 2, 2);
  font-size: 20px;
}
table ion-icon[name="trash"]:hover {
  color: rgb(206, 3, 3);
  cursor: pointer;
}
table ion-icon[name="create"] {
  color: rgb(75, 7, 92);
  font-size: 20px;
}
