.nav-link:hover {
  -webkit-transition: all 0.9s;
  transition: all 0.9s;
}

.nav-link-collapse:after {
  float: right;
  content: "\f067";
  font-family: "FontAwesome";
}

.nav-link-show:after {
  float: right;
  content: "\f068";
  font-family: "FontAwesome";
}

.nav-item ul.nav-second-level {
  padding-left: 0;
}
.nav-link {
  display: flex;
  justify-content: center;
