.menu-icons ion-icon,
.menu ion-icon {
  font-size: 25px;
}
.menu-icons ion-icon[name="close-circle-outline"] {
  color: red;
}
.menu-icons ion-icon[name="create-outline"] {
  color: rgb(148, 5, 148);
}
.menu ion-icon[name="add-circle-outline"] {
  color: rgb(255, 255, 255);
}
.menu {
  position: absolute;
  bottom: 3px;
  right: 3px;
  background-color: rgb(114, 112, 255);
  border-radius: 50%;
}
